/*carousel control button*/

.custom-carousel {
  .owl-controls {
    position: absolute;
    right: 0;
    top: -25px;
    width: 100%;
    display: block;

    .owl-prev {
      position: absolute;
      width: 20px;
      height: 20px;
      top: -36px;
      right: 27px;
      -webkit-transition: all linear 0.2s;
      -moz-transition: all linear 0.2s;
      -ms-transition: all linear 0.2s;
      -o-transition: all linear 0.2s;
      transition: all linear 0.2s;
      -webkit-border-radius: 3px;
      -moz-border-radius: 3px;
      border-radius: 3px;
      background: #dddddd;

      &:before {
        color: #fff;
        content: "\f104";
        font-family: fontawesome;
        font-size: 13px;
        left: 8px;
        position: absolute;
        top: 0px;
      }
    }
    .owl-next {
      position: absolute;
      width: 20px;
      height: 20px;
      top: -36px;
      right: 0px;
      -webkit-transition: all linear 0.2s;
      -moz-transition: all linear 0.2s;
      -ms-transition: all linear 0.2s;
      -o-transition: all linear 0.2s;
      transition: all linear 0.2s;
      -webkit-border-radius: 3px;
      -moz-border-radius: 3px;
      border-radius: 3px;
      background: #dddddd;
      &:before {
        content: "\f105";
        font-family: fontawesome;
        color: #fff;
        font-size: 13px;
        left: 8px;
        position: absolute;
        top: 0px;
      }
    }
  }
}

